.AboutView {
  margin: 50px 20px;
}

.AboutView button, .AboutView select {
  color: inherit;
  background: rgba(211, 211, 211, .5);
  border: 1px solid #d3d3d3;
}

.exportOptions {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.AboutView em {
  margin: 5px 0;
}

.exportUrl {
  display: -ms-flexbox;
  display: flex;
  margin: 15px 0;
  max-width: 640px;
}

.url {
  margin: 0;
  width: 100%;
  font-size: 1em;
  font-family: monospace;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}

.exportUrl button {
  width: 100px;
  height: 40px;
}

.zoomrange {
  vertical-align: middle;
}



