/*  Main css file */

body {
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}

a {
  color: inherit;
  -webkit-filter: invert(30%);
          filter: invert(30%);
}

.gridItem {
  overflow: hidden;
}

.gridItem.resizeable {
  -webkit-animation-duration: .3s;
          animation-duration: .3s;
  -webkit-animation-name: bgcolor;
          animation-name: bgcolor;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes bgcolor {
  from {
    background-color: transparent;
  }
  to {
    background-color: #eee;
  }
}

@keyframes bgcolor {
  from {
    background-color: transparent;
  }
  to {
    background-color: #eee;
  }
}

.appbar {
  top: 0;
  right: 0;
  position: fixed;
  background-color: transparent;
  -webkit-transform: translate3d(calc(100% - 52px), 0, 0);
          transform: translate3d(calc(100% - 52px), 0, 0);
  -webkit-transition: background-color .3s linear, -webkit-transform .3s ease-in-out;
  transition: background-color .3s linear, -webkit-transform .3s ease-in-out;
  -o-transition: transform .3s ease-in-out, background-color .3s linear;
  transition: transform .3s ease-in-out, background-color .3s linear;
  transition: transform .3s ease-in-out, background-color .3s linear, -webkit-transform .3s ease-in-out;
  z-index: 2;
  will-change: transform;
}

.icon {
  width: 34px;
  height: 34px;
  vertical-align: middle;
}

.appbar button {
  background: transparent;
  border: none;
  cursor: pointer;
  margin: 4px;
  outline: none;
}

.appbar.open {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}

.appbar button:first-child {
  -webkit-transition: color .3s linear, -webkit-transform .3s ease-in-out;
  transition: color .3s linear, -webkit-transform .3s ease-in-out;
  -o-transition: transform .3s ease-in-out, color .3s linear;
  transition: transform .3s ease-in-out, color .3s linear;
  transition: transform .3s ease-in-out, color .3s linear, -webkit-transform .3s ease-in-out;
}

.appbar.open button:first-child {
  -webkit-transform: rotate(-120deg);
      -ms-transform: rotate(-120deg);
          transform: rotate(-120deg);
}

.addDevices {
  margin: 50px 20px;
}

.addDevices i {
  color: #666;
  vertical-align: middle;
}

.addDevices .icon {
  width: 24px;
  height: 24px;
}

.layout {
  margin-bottom: 50px;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
}

.footer {
  position:fixed;
  left:0;
  bottom:0;
  width: 100%;
  z-index: 2;
  -webkit-transition: -webkit-transform .3s ease-out;
  transition: -webkit-transform .3s ease-out;
  -o-transition: transform .3s ease-out;
  transition: transform .3s ease-out;
  transition: transform .3s ease-out, -webkit-transform .3s ease-out;
  will-change: transform;
}

.footer.hidden {
  -webkit-transform: translate3d(0, 50px, 0);
          transform: translate3d(0, 50px, 0);
}

.footer .left {
  float: left;
}

.footer .title {
  text-align: center;
}

.footer .right {
  float: right;
}

.footer button {
  background: transparent;
  border: none;
  cursor: pointer;
  margin: 4px;
  outline: none;
}

.zoomlabel {
    font-style: italic;
    font-size: small;
    padding: 2px;
    text-align: center;
}



